import React, { Component } from 'react';
// import logo from '../logo.svg';
import './App.css';
import NavBar from "../Components/NavBar/NavBar";
import PersonsList from "../Components/PersonsList/PersonsList";
import Footer from "../Components/Footer/Footer";
import UserInput from "../Components/UserInput/UserInput";
import Technology from "../Components/Technology/Technology";


import { Route } from "react-router-dom";

class App extends Component {

  constructor(props) {
    super(props);
    this.state= {
    	persons : [
        {name: 'Dev', age: 23},
        {name: 'Max', age: 28},
        {name: 'John', age: 24},
        {name: 'RV', age: 22}
      ]  
    }
  }

  // Life cycle methods
  
  inputChangeHandler = (event, index) => {
    let copy_persons = [...this.state.persons];
    copy_persons[index].name = event.target.value;


    this.setState({
      persons : copy_persons
    })
  }

  deletePerson = (index) => {
    
    let new_persons = this.state.persons.slice(); //will copy the persons state array into new array
    new_persons.splice(index,1);
    // console.log(this.state.persons)

    this.setState({
     persons:new_persons 
    });
  }

  resetPersonState = () => {
    this.setState({
      persons : [
        {name: 'Dev', age: 23},
        {name: 'Max', age: 28},
        {name: 'John', age: 24},
        {name: 'RV', age: 22}
      ],
     // team_data : []
    })
  }

  render() {
    // console.log(this.state)
  
    return (
      <div className="">

        <NavBar app_title="Manage Persons" />

        <Route path="/" exact render={() => <div><h1>Welcome to Team Management MVP</h1> <p>Beta Version (Under Development)</p> <p>Click on Manage Team to get an overview</p> </div>} />

        <Route path="/manage-team" component={PersonsList} />

        <Route path="/contact" component={UserInput} />
        
        <Route path="/technology" component={Technology} />

        <Footer />

      </div>

    );
  }
}

export default App;
