import React, { Component } from 'react';
import Person from "./Person/Person";

class   PersonsList extends Component {
    constructor(props){
        super(props);

        this.state= {            
          showPersons : false,
          user_data : [],
          team_data: []
        }
    }

    // Life cycle methods
    componentDidMount(){
        console.log("component mounted")

        //https://www.googleapis.com/books/v1/volumes?q=isbn:0747532699 - BOOKS API
        
        // API to get random user details in the response.
        fetch("https://randomuser.me/api/?results=10", {
        headers : { 
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        }

        })
        .then(response => response.json())
        .then((response) => {
        this.setState({user_data:response.results});

        console.log(this.state.user_data)

        console.log(this.state.user_data[0].location.city)
        //console.log(typeof(Object.values(this.state.data)))

        })

    }

    togglePersonFlag = () => {
        this.setState({
          showPersons: !this.state.showPersons
        });
    }

    pickTeamMember = (index, full_name, age, user_img) => {
        //team members not more than 3
        console.log(this.state.team_data.length + 'length');
        if(this.state.team_data.length > 2) {
          alert('You can not pick more than 3 members in your team');
          return false;
        }
        else {
          let available_users = this.state.user_data.slice();
          available_users.splice(index,1);
    
          let updated_team = [...this.state.team_data];
          updated_team.push({name: full_name,age: age, image: user_img});
    
          this.setState({
              user_data:available_users,
              team_data: updated_team
            });
          }
    
    }

    // removePersonFromTeam = (i, full_name, age, user_img) => {
    //     let team = this.state.team_data.slice();
    //     team.splice(i,1);

    //     let available_users = [...this.state.user_data];
    //     available_users.push({name: full_name,age: age, image: user_img});

    //     this.setState({
    //         user_data:available_users,
    //         team_data: team
    //       });
    //     }

    resetPersonState = () => {
        this.setState({
          persons : [
            {name: 'Dev', age: 23},
            {name: 'Max', age: 28},
            {name: 'John', age: 24},
            {name: 'RV', age: 22}
          ],
          team_data : []
        })
    }

    render() {
        console.log(this.state);

        let available_users = null;
        let help_msg = 'Click Show Team button to view team details using HTTP request';

        if(this.state.showPersons) {
            available_users = this.state.user_data.map( (person,i) => {
                let full_name = person.name.first + person.name.last
                return <Person 
                        key={i} name={full_name} age={person.dob.age} 
                        avatar_img={person.picture.thumbnail}
                        // change={(event) => this.props.changeHandler(event, i)} 
                        click={() => this.pickTeamMember(i,full_name, person.dob.age, person.picture.thumbnail)}
                        btnLable='Pick'/>
                });
            help_msg = '';
        }

        let team_pick_msg = 'Select any team member from the available Users';
        let team_list = null;
        let resetBtn = null;
        if(this.state.team_data.length > 0) {
            team_pick_msg='';

            resetBtn= <button className="btn btn-success btn-sm" onClick={this.resetPersonState}>Reset</button>;

            team_list = this.state.team_data.map( (person,i) => {
            return <Person 
                    key={i} name={person.name} age={person.age} 
                    avatar_img={person.image}                    
                    team_click={() => this.removePersonFromTeam(i, person.name, person.age, person.image)} 
                    />
                    
            })
        }

        // set dynamic class for show/hide button
        const classes = [];
        if(this.state.showPersons) classes.push('btn btn-warning')
        else classes.push('btn btn-primary')

        return (
            <div className="container">
                
                    <p>{help_msg}</p>
                    <button className={classes.join(' ')} onClick={this.togglePersonFlag}>{this.state.showPersons ? 'Hide' : 'Show' } Resources</button>
                

                <div className="row">
                    <div className="col-md-8">
                        <p>Available Resources</p>
                        {available_users}
                    </div>
                    <div className="col-md-4">
                        <p>Your Team &nbsp; &nbsp; &nbsp; 
                            <span> {resetBtn} </span> 
                        </p>
                        <p>{team_pick_msg}</p>
                        {team_list}
                    </div>
                </div>
                
                
            </div>
        );
    }
}

export default PersonsList;