import React, { Component } from 'react';

class UserInput extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email : false
        }
        
    }
    
    submitHandler = () => {
        this.setState({
            email : true
        });
    }

    render() {
        const inlinestyle = {
            margin: "10px",
            padding: "10px"
        }

        let body = (
            <p>Your Email id : <input type="text"  value="@gmail.com" autoFocus/> 
                    <span>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<button className="btn btn-primary" onClick={this.submitHandler}>Submit</button> </span>
            </p>
        );
        if(this.state.email) body = <p>Your email is received. Our team will contact you on next business day</p>

        return (
            <div style={inlinestyle}>
                {body}                
            </div>
        );
    }
}

export default UserInput;