import React, { Component} from 'react';

class Person extends Component {

    constructor(props) {
        super(props);
        
    }
    
    render() {
            // Inline CSS
            const divStyle = {
            border: '2px solid black',
            boxShadow: '0px 1px 1px black',
            margin: '15px',
            width: '300px',
            height: '180px',
            padding: '10px',
            display: 'inline-block'

        }

        let btnBlock = null;
        if(this.props.btnLable != null) btnBlock = <button className="btn btn-primary btn-sm" onClick={this.props.click}>{this.props.btnLable}</button>

        return (
        	<div style={divStyle}>
                <div className="row">
                    <div className="col-md-4">
                        <img src={this.props.avatar_img} alt="user" style={{borderRadius: '50%'}}/>
                    </div>
                    <div className="col-md-8">
                        <p>Name: {this.props.name} </p>
                        <p>Age: {this.props.age}</p>
                        {btnBlock}
                    </div>
                </div>
                <input type="text" onChange={this.props.change} value={this.props.name} />
            </div>
        );
    }
}

export default Person;
